import { Component, ViewChild, ViewContainerRef, ComponentFactoryResolver} from '@angular/core';
import { HtmlParser } from '@angular/compiler';
  
@Component({
  templateUrl: './select.component.html'
})

export class SelectComponent{
  food = 'fish';
  foods = [{
    label: '',
    value: 'wuchang',
  },{
    label: '',
    value: 'fish',
  },{
    label: '',
    value: 'niuwa',
  },{
    label: '',
    value: 'zhuti',
  }]
  handleChange(ev){
    console.log('option',ev)
  }

  city = 'bj'
  cities = [{
    label: '',
    value: 'bj',
  },{
    label: '',
    value: 'sh',
  },{
    label: '',
    value: 'gz',
  },{
    label: '',
    value: 'sz',
  }]
  handleChange1(ev){
    console.log('option1',ev)
  }

  options = [{
    value: 'hubei',
    label: '',
    children: [{
      value: 'wuhan',
      label: '',
      children: [{
        value: 'wuchang',
        label: '',
      },{
        value: 'hankou',
        label: '',
      },{
        value: 'hanyang',
        label: '',
      }]
    },{
      value: 'xiangyang',
      label: '',
      children: [{
        value: 'fancheng',
        label: '',
      },{
        value: 'laohekou',
        label: '',
      }]
    }]
  },{
    value: 'henan',
    label: '',
    children: [{
      value: 'zhengzhou',
      label: '',
      children: [{
        value: 'zz1',
        label: '1',
      },{
        value: 'zz2',
        label: '2',
      },{
        value: 'zz3',
        label: '3',
      }]
    },{
      value: 'anyang',
      label: '',
      children: [{
        value: 'ay1',
        label: '1',
      },{
        value: 'ay2',
        label: '2',
      }]
    }]
  }]

  chosen1;
  chosen1Child;
  chosen2;
  chosen2Child;
  chosen3;
  
  handle1(val){
    this.chosen1 = val
    this.chosen1Child = this.options.find(item => item.value == val).children
  }

  handle2(val){
    this.chosen2 = val
    this.chosen2Child = this.chosen1Child.find(item => item.value == val).children
  }

  handle3(val){
    this.chosen3 = val
  }
  
}



